import ScenarioItem from "./ScenarioItem.js";
import { CHECKLIST_META_DATA } from "@meddbriefer/scenario-data/traumaScenarioV2MetaData.js";
import classnames from 'classnames';

const scenarioData = CHECKLIST_META_DATA.filter(e => !e.id.includes("critical") && e.type !== "checklist-prompt");

const Steplist = ({ active, scenario }) => {
	return (
		<div id="scenario-steps" className={classnames({"scenario-view-hidden": !active})}>
			<h1>Scenario Items</h1>
			{scenarioData.map((item) => (
				<ScenarioItem key={item.id} item={item} scenario={scenario} />
			))}
		</div>
	);
};

